/* Minimalist Design with Brand Colors */
:root {
    /* Brand Colors */
    --brand-primary: #CF9B06;
    --brand-secondary: #60AD9B;
    --brand-accent: #FECF33;
    --brand-white: #FFFFFF;
    
    /* Derived Colors */
    --primary-color: var(--brand-primary);
    --primary-dark: #a67d05;
    --primary-light: #e6b82e;
    --secondary-color: var(--brand-secondary);
    --secondary-dark: #4d8a7a;
    --secondary-light: #7bc4b0;
    --accent-color: var(--brand-accent);
    
    /* Neutral Colors */
    --bg-color: var(--brand-white);
    --bg-secondary: #fafafa;
    --border-color: #e5e5e5;
    --text-primary: #1a1a1a;
    --text-secondary: #666666;
    --text-muted: #999999;
    
    /* Status Colors */
    --success: var(--brand-secondary);
    --error: #dc3545;
    --warning: var(--brand-accent);
    
    /* Shadows */
    --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Arian AMU font for Armenian text */
@font-face {
    font-family: 'Arian AMU';
    src: local('Arian AMU'), local('ArianAMU'), local('ArianAMU-Regular');
    font-weight: normal;
    font-style: normal;
    unicode-range: U+0530-058F, U+FB00-FB17;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
    background: var(--bg-secondary);
    color: var(--text-primary);
    line-height: 1.6;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

/* Armenian text styling */
[lang="hy"], .armenian-text, textarea[value*="ա"], textarea[value*="Ա"] {
    font-family: 'Arian AMU', 'Noto Sans Armenian', 'Segoe UI', sans-serif;
}

html {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

/* Login Page */
.login-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #eee;
    padding: 20px;
    position: relative;
}

.login-card {
    background: var(--bg-color);
    border-radius: 12px;
    padding: 40px;
    width: 100%;
    max-width: 480px;
    box-shadow: var(--shadow-lg);
    border: 1px solid var(--border-color);
    position: relative;
    z-index: 1;
}

.login-logo-container {
    text-align: center;
    margin-bottom: 32px;
}

.login-logo {
    width: 80px;
    height: 80px;
    margin-bottom: 16px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.login-platform-name {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0;
}

.login-header {
    text-align: center;
    margin-bottom: 32px;
}

.login-title {
    font-size: 28px;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 8px;
}

.login-description {
    color: var(--text-secondary);
    font-size: 14px;
}

.form-group {
    margin-bottom: 20px;
}

.form-label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: var(--text-primary);
    margin-bottom: 8px;
}

.form-input {
    width: 100%;
    padding: 12px 16px;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    font-size: 16px;
    background: var(--bg-color);
    color: var(--text-primary);
    transition: all 0.2s;
}

.form-input:focus {
    outline: none;
    border-color: var(--brand-primary);
    box-shadow: 0 0 0 3px rgba(207, 155, 6, 0.1);
}

.form-select {
    width: 100%;
    padding: 12px 16px;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    font-size: 16px;
    background: var(--bg-color);
    color: var(--text-primary);
    cursor: pointer;
    transition: all 0.2s;
}

.form-select:focus {
    outline: none;
    border-color: var(--brand-primary);
    box-shadow: 0 0 0 3px rgba(207, 155, 6, 0.1);
}

.form-textarea {
    width: 100%;
    min-height: 120px;
    padding: 12px 16px;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    font-size: 16px;
    resize: vertical;
    background: var(--bg-color);
    color: var(--text-primary);
    font-family: inherit;
}

.form-textarea:focus {
    outline: none;
    border-color: var(--brand-primary);
    box-shadow: 0 0 0 3px rgba(207, 155, 6, 0.1);
}

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 12px 24px;
    border-radius: 6px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
    border: none;
    gap: 8px;
}

.btn-primary {
    background: var(--brand-primary);
    color: white;
}

.btn-primary:hover {
    background: var(--primary-dark);
    transform: translateY(-1px);
    box-shadow: var(--shadow-md);
}

.btn-primary:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
}

.btn-secondary {
    background: var(--brand-secondary);
    color: white;
}

.btn-secondary:hover {
    background: var(--secondary-dark);
    transform: translateY(-1px);
    box-shadow: var(--shadow-md);
}

.btn-accent {
    background: var(--brand-accent);
    color: var(--text-primary);
}

.btn-accent:hover {
    background: #e6b82e;
    transform: translateY(-1px);
    box-shadow: var(--shadow-md);
}

.btn-block {
    width: 100%;
}

.btn-outline {
    background: transparent;
    border: 1px solid var(--border-color);
    color: var(--text-primary);
}

.btn-outline:hover {
    background: var(--bg-secondary);
    border-color: var(--brand-primary);
    color: var(--brand-primary);
}

.btn-sm {
    padding: 8px 16px;
    font-size: 14px;
}

/* Dashboard Layout */
.dashboard-container {
    display: flex;
    min-height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.sidebar {
    width: 260px;
    background: var(--bg-color);
    border-right: 1px solid var(--border-color);
    display: flex;
    flex-direction: column;
    position: fixed;
    height: 100vh;
    left: 0;
    top: 0;
    box-shadow: var(--shadow);
    transition: width 0.3s ease;
    z-index: 1000;
}

.sidebar.collapsed {
    width: 60px;
    min-width: 60px;
}

.sidebar-header {
    padding: 32px 24px;
    border-bottom: 1px solid var(--border-color);
    background: #60AD9B;
    color: white;
    position: relative;
    min-height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.sidebar-logo-container {
    margin-bottom: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar-logo {
    width: 80px;
    height: 80px;
    object-fit: contain;
    filter: brightness(0) invert(1);
}

.sidebar-toggle {
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(255, 255, 255, 0.2);
    border: none;
    color: white;
    width: 32px;
    height: 32px;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.2s;
}

.sidebar-toggle:hover {
    background: rgba(255, 255, 255, 0.3);
}

.sidebar.collapsed .sidebar-header {
    padding: 24px 12px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar-header h2 {
    font-size: 16px;
    font-weight: 600;
    color: white;
    margin: 0 0 8px 0;
    line-height: 1.4;
    text-transform: uppercase;
}

.sidebar-header small {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.9);
    display: block;
    margin-top: 4px;
}

.sidebar-menu {
    flex: 1;
    padding: 16px 0;
    overflow-y: auto;
}

.menu-item {
    display: flex;
    align-items: center;
    padding: 12px 24px;
    color: var(--text-secondary);
    text-decoration: none;
    transition: all 0.2s;
    gap: 12px;
    border-left: 3px solid transparent;
}

.menu-item:hover {
    background: var(--bg-secondary);
    color: var(--brand-primary);
}

.menu-item.active {
    background: rgba(207, 155, 6, 0.1);
    color: var(--brand-primary);
    border-left-color: var(--brand-primary);
    font-weight: 500;
}

.sidebar-footer {
    padding: 20px;
    border-top: 1px solid var(--border-color);
    background: var(--bg-secondary);
}

.user-info {
    display: flex;
    align-items: center;
    gap: 12px;
}

.user-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--brand-primary), var(--brand-secondary));
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 600;
    font-size: 14px;
}

.user-details {
    flex: 1;
    min-width: 0;
}

.user-name {
    font-weight: 500;
    color: var(--text-primary);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.user-role {
    font-size: 12px;
    color: var(--text-secondary);
}

.main-content {
    margin-left: 260px;
    width: calc(100vw - 260px);
    padding: 32px;
    transition: margin-left 0.3s ease, width 0.3s ease;
    overflow-x: hidden;
}

.sidebar.collapsed ~ .main-content {
    margin-left: 60px;
    width: calc(100vw - 60px);
}

.content-header {
    margin-bottom: 32px;
}

.page-title {
    font-size: 28px;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 8px;
    background: linear-gradient(135deg, var(--brand-primary), var(--brand-secondary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.page-description {
    color: var(--text-secondary);
    font-size: 16px;
}

/* Translation Table */
.translation-table {
    background: var(--bg-color);
    border-radius: 8px;
    border: 1px solid var(--border-color);
    overflow: hidden;
    box-shadow: var(--shadow);
}

.table-header {
    padding: 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.table-title {
    font-size: 18px;
    font-weight: 600;
    color: var(--text-primary);
}

.table-responsive {
    overflow-x: auto;
}

.translation-row {
    display: grid;
    grid-template-columns: 200px 1fr 1fr 1fr 1fr 1fr 200px 80px;
    gap: 1px;
    background: var(--border-color);
    border-bottom: 1px solid var(--border-color);
}

.translation-row:first-child {
    background: linear-gradient(135deg, var(--brand-primary), var(--brand-secondary));
    font-weight: 600;
    font-size: 14px;
    color: white;
    position: sticky;
    top: 0;
    z-index: 10;
}

.translation-cell {
    background: var(--bg-color);
    padding: 16px;
    min-height: 60px;
    display: flex;
    align-items: center;
}

.translation-cell.header {
    background: transparent;
    font-weight: 600;
    font-size: 13px;
    color: white;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.translation-input {
    width: 100%;
    border: none;
    background: transparent;
    color: var(--text-primary);
    font-size: 14px;
    resize: none;
    min-height: 40px;
    padding: 8px;
    font-family: inherit;
    border-radius: 4px;
}

.translation-input:focus {
    outline: 2px solid var(--brand-primary);
    outline-offset: -2px;
    background: rgba(207, 155, 6, 0.05);
}

.translation-input:disabled {
    background: var(--bg-secondary);
    color: var(--text-muted);
    cursor: not-allowed;
}

.translation-input.english-ref {
    background: rgba(96, 173, 155, 0.1);
    color: var(--text-secondary);
    font-style: italic;
}

.checkbox-cell {
    display: flex;
    align-items: center;
    justify-content: center;
}

.checkbox {
    width: 20px;
    height: 20px;
    cursor: pointer;
    accent-color: var(--brand-primary);
}

.checkbox:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

/* Language Selector */
.language-selector {
    display: flex;
    gap: 8px;
    background: var(--bg-color);
    padding: 8px;
    border-radius: 8px;
    box-shadow: var(--shadow);
    border: 1px solid var(--border-color);
}

.lang-btn {
    padding: 6px 12px;
    border: 1px solid var(--border-color);
    background: transparent;
    color: var(--text-secondary);
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 500;
    transition: all 0.2s;
    white-space: nowrap;
}

.lang-btn.active {
    background: var(--brand-primary);
    color: white;
    border-color: var(--brand-primary);
    box-shadow: var(--shadow);
}

.lang-btn:hover:not(.active) {
    background: var(--bg-secondary);
    border-color: var(--brand-primary);
    color: var(--brand-primary);
}

/* Alerts */
.alert {
    padding: 12px 16px;
    border-radius: 6px;
    margin-bottom: 20px;
    border: 1px solid;
}

.alert-success {
    background: rgba(96, 173, 155, 0.1);
    border-color: var(--brand-secondary);
    color: var(--secondary-dark);
}

.alert-error {
    background: rgba(220, 53, 69, 0.1);
    border-color: var(--error);
    color: var(--error);
}

/* Loading */
.loading {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 2px solid var(--border-color);
    border-top-color: var(--brand-primary);
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

/* Empty State */
.empty-state {
    text-align: center;
    padding: 60px 20px;
    color: var(--text-secondary);
}

.empty-icon {
    font-size: 48px;
    margin-bottom: 16px;
    opacity: 0.5;
}

.empty-title {
    font-size: 18px;
    font-weight: 500;
    color: var(--text-primary);
    margin-bottom: 8px;
}

.empty-description {
    color: var(--text-secondary);
    margin-bottom: 24px;
}

/* Stats Grid */
.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 24px;
    margin-bottom: 32px;
}

.stat-card {
    background: var(--bg-color);
    border-radius: 8px;
    padding: 24px;
    box-shadow: var(--shadow);
    border: 1px solid var(--border-color);
    transition: all 0.2s;
    position: relative;
    overflow: hidden;
}

.stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: #3A3A3B;
}

.stat-card:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
}

.stat-icon {
    width: 48px;
    height: 48px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16px;
    font-size: 20px;
    background: rgba(207, 155, 6, 0.1);
    color: var(--brand-primary);
}

.stat-icon.secondary {
    background: rgba(96, 173, 155, 0.1);
    color: var(--brand-secondary);
}

.stat-icon.accent {
    background: rgba(254, 207, 51, 0.1);
    color: #b8860b;
}

.stat-value {
    font-size: 32px;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 8px;
    line-height: 1;
}

.stat-label {
    color: var(--text-secondary);
    font-size: 14px;
    font-weight: 500;
}

/* Table Container */
.table-container {
    background: var(--bg-color);
    border-radius: 8px;
    box-shadow: var(--shadow);
    border: 1px solid var(--border-color);
    overflow: hidden;
    margin-bottom: 32px;
}

.data-table {
    width: 100%;
    border-collapse: collapse;
}

.data-table th {
    padding: 16px;
    text-align: left;
    font-weight: 600;
    color: var(--text-primary);
    background: var(--bg-secondary);
    border-bottom: 2px solid var(--border-color);
    font-size: 14px;
}

.data-table td {
    padding: 16px;
    border-bottom: 1px solid var(--border-color);
    font-size: 14px;
}

.data-table tr:hover {
    background: var(--bg-secondary);
}

.status-badge {
    display: inline-flex;
    align-items: center;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 12px;
    font-weight: 500;
}

.status-badge.confirmed {
    background: rgba(96, 173, 155, 0.15);
    color: var(--secondary-dark);
}

.status-badge.pending {
    background: rgba(207, 155, 6, 0.15);
    color: var(--primary-dark);
}

.status-badge.active {
    background: rgba(207, 155, 6, 0.15);
    color: var(--primary-dark);
}

/* Modal */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    padding: 20px;
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.modal-content {
    background: var(--bg-color);
    border-radius: 12px;
    padding: 32px;
    max-width: 500px;
    width: 100%;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: var(--shadow-lg);
    border: 1px solid var(--border-color);
    animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.modal-header {
    margin-bottom: 24px;
    padding-bottom: 16px;
    border-bottom: 1px solid var(--border-color);
}

.permissions-container {
    background: var(--bg-color);
    border-radius: 8px;
    padding: 24px;
    box-shadow: var(--shadow);
}

.permissions-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 16px;
    margin-top: 24px;
}

.permission-item {
    padding: 12px;
    background: var(--bg-secondary);
    border-radius: 6px;
    border: 1px solid var(--border-color);
}

.modal-title {
    font-size: 20px;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 8px;
}

.modal-body {
    margin-bottom: 24px;
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    padding-top: 16px;
    border-top: 1px solid var(--border-color);
}

/* Responsive */
@media (max-width: 1024px) {
    .sidebar {
        width: 200px;
    }
    
    .main-content {
        margin-left: 200px;
        max-width: calc(100vw - 200px);
    }
    
    .translation-row {
        grid-template-columns: 150px 1fr 1fr 1fr 1fr 1fr 150px 60px;
    }
}

@media (max-width: 768px) {
    .sidebar {
        display: none;
    }
    
    .main-content {
        margin-left: 0;
        max-width: 100vw;
        padding: 16px;
    }
    
    .translation-row {
        grid-template-columns: 120px 1fr 1fr 1fr 1fr 1fr 120px 50px;
        font-size: 12px;
    }
    
    .translation-cell {
        padding: 12px;
    }
}
