/* Import Minimalist Design */
@import url('./styles/minimal.css');

body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Noto Sans Armenian', -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

